<template>
    <div id="new-emp">
        <div class="row">
            <form @submit.prevent="saveEmployee()" class="col s12">
                <div class="row"><div class="input-field col s12"><input type="text" v-model="emp_id"><label>Employee_ID#</label></div></div>
                <div class="row"><div class="input-field col s12"><input type="text" v-model="name"><label>Employee Name</label></div></div>
                <div class="row"><div class="input-field col s12"><input type="text" v-model="dept"><label>Department</label></div></div>
                <div class="row"><div class="input-field col s12"><input type="text" v-model="position"><label>Position</label></div></div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Back</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './FirebaseInit.js'
export default {
    name : 'new-emp',
    data () {
        return {
            emp_id:null,
            name:null,
            dept:null,
            position:null
        }
    },
    methods: {
        saveEmployee() {
            if(this.emp_id == null || this.name == null ||this.dept == null ||this.position == null){
                alert('all fields are compulsory!!')
            }
            else{
                db.collection('employee').add(
                {
                emp_id:this.emp_id,
                emp_name:this.name,
                dept:this.dept,
                position:this.position
                    }
                ).then(docRef => this.$router.push('/'))
                .catch(error => console.log(error)
                )
            }
            
        }
    }
}
</script>




