<template>
    <div  id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Employees</h4></li>
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
                <div class="chip">{{employee.dept}}</div>{{employee.emp_id}}:{{employee.emp_name}}

                <router-link class="secondary-content" v-bind:to="{name:'view-emp', params: {emp_id:employee.emp_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
            <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './FirebaseInit.js'

export default {
    name : 'Dashboard',
    data () {
        return {
            employees:[]
        }
    },
    created (){
        db.collection('employee').orderBy('dept').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                'emp_id':doc.data().emp_id,
                'emp_name':doc.data().emp_name,
                'position':doc.data().position,
                'dept':doc.data().dept
                }
            this.employees.push(data)                
            })
        })
    }

}
</script>




