<template>
    <div id="view-emp">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name | capitalize}}</h4></li>
            <li class="collection-item"> <strong> Employee ID:</strong> {{emp_id}}</li>
            <li class="collection-item"> <strong> Department:</strong> {{dept}}</li>
            <li class="collection-item"> <strong> position:</strong> {{position}}</li>
        </ul>

        <router-link to='/' class="btn grey hover">BACK</router-link>
        <button @click="deleteEmployee()" class="btn red hover">DELETE</button>


        <div class="fixed-action-btn">
            <router-link v-bind:to="{ name: 'edit-emp',
            params:{emp_id:emp_id}}" class="btn-floating btn-large red">
            <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './FirebaseInit.js'
import { functions } from 'firebase';
export default {
    name : 'view-emp',
    data () {
        return {
            emp_id: null,
            name: null,
            dept: null,
            position:null
        }
    },
    beforeRouteEnter (to, from, next) {
     db.collection('employee').where('emp_id', '==', to.params.emp_id).get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.emp_id = doc.data().emp_id
                    vm.name = doc.data().emp_name
                    vm.dept = doc.data().dept
                    vm.position = doc.data().position
                })
            })
        })
    },
    watch: {
        '$route': 'fetchdata'
    },
    methods: {
        fetchdata() {
            console.log('fuck this method');
            
             db.collection('employee').where('emp_id','==',this.$route.params.emp_id).get()
              .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                  this.emp_id = doc.data().emp_id
                    this.name = doc.data().name
                    this.dept = doc.data().dept
                    this.position = doc.data().position
            })
              }
              )},
              deleteEmployee() {
            if(confirm('do you want to delete this employee')){
                db.collection('employee').where('emp_id','==',this.$route.params.emp_id).get()
              .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                  doc.ref.delete()
                  this.$router.push('/')
            })
              }
              )
            }
            
             }
    },
    filters : {
        capitalize : function(value) {
            if(!value) return ''
            value = value.toString()
            return value.toUpperCase() 
        }
    }
}
</script>




