<template>
    <div id="edit-emp">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="editEmployee()" class="col s12">
                <div class="row"><div class="input-field col s12"><input type="text" v-model="emp_id" disabled></div></div>
                <div class="row"><div class="input-field col s12"><input type="text" v-model="name"></div></div>
                <div class="row"><div class="input-field col s12"><input type="text" v-model="dept"></div></div>
                <div class="row"><div class="input-field col s12"><input type="text" v-model="position"></div></div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Back</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import  db from './FirebaseInit.js'
export default {
    name : 'edit-emp',
    data () {
        return {
            emp_id: null,
            name: null,
            dept: null,
            position:null
        }
    },
    beforeRouteEnter (to, from, next) {
     db.collection('employee').where('emp_id', '==', to.params.emp_id).get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.emp_id = doc.data().emp_id
                    vm.name = doc.data().emp_name
                    vm.dept = doc.data().dept
                    vm.position = doc.data().position
                })
            })
        })
    },
    watch: {
        '$route': 'fetchdata'
    },
    methods: {
        fetchdata() {
             db.collection('employee').where('emp_id','==',this.$route.params.emp_id).get()
              .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                  this.emp_id = doc.data().emp_id
                    this.name = doc.data().emp_name
                    this.dept = doc.data().dept
                    this.position = doc.data().position
            })
              }
              )},
              editEmployee() {
            if(confirm('sure to UPDATE this employee ?')){
                db.collection('employee').where('emp_id','==',this.$route.params.emp_id).get()
              .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                  doc.ref.update({
                    //   emp_id:this.emp_id,
                      emp_name:this.name,
                      dept:this.dept,
                      position:this.position
                  })
                  this.$router.push({name:'view-emp',params:{emp_id:this.emp_id}})
            })
              }
              )
            }
            
             }
    }
}
</script>




